export function isClassName(el, className) {// 
    return el.className.indexOf(className) !== -1
}
export function addClass(el, className) {    // 
    var isClassName = el.className.indexOf(className) !== -1
    // 
    if (!isClassName(el, className)) { el.className = el.className + ' ' + className }
}
export function removeClass(el, className) { // 
    var isClassName = el.className.indexOf(className) !== -1
    // 
    if (isClassName) {
        el.className = (' ' + el.className).replace(' ' + className, '').trim()
    }
}
export function toggleClass(el, className) { // 
    // addClass() removeClass()
    var isClassName = el.className.indexOf(className) !== -1
    el.className = isClassName ? removeClass(el, className) : addClass(el, className)
}
export function forIn(object, callback) {    // for in
    for (var key in object) {
        if (object.hasOwnProperty(key)) {
            callback(key, object[key])
        }
    }
}
export function splitArray(arr, len) { // 
    let arr_length = arr.length;
    let newArr = [];
    for (let i = 0; i < arr_length; i += len) {
        console.log(i)
        newArr.push(arr.slice(i, i + len));
        console.log(i)
    }
    return newArr;
}
export function lockNumber(number, leftInter, rightInter) { //  [leftInterval, rightInterval]
    if (number < leftInterval) { number = leftInterval }
    if (number > rightInterval) { number = rightInterval }
    return number // 
}
export function isArray(val) { // 
    return Object.prototype.toString.call(val) === '[object Array]'
    return Array.isArray(val)
    return val.constructor === Array
    return val.__proto__ === Array.prototype
    return val instanceof Array
}
export function isMobile(str) { // 
    return /^1[3456789]\d{9}$/.test(str)
}
export function isEmail(str) { // 
    return /[A-Za-z0-9._%+-]+@@[A-Za-z0-9.-]+\.[A-Za-z]{2,4}/.test(str)
}
export function isStrCode(str) { // 
    return /<[\/\!]*[^<>]*>/ig.test(str)
}
export function removeStrCode(str) { // 
    return str.replace(/<[\/\!]*[^<>]*>/ig, "")
}
