<template>
  <div ref="wrapper">
    <slot><!--  --></slot>
  </div>
</template>

<script>
import BScroll from "@better-scroll/core";
export default {
  props: ["click", "scrollX", "scrollY"],
  data: () => ({
    options: { // BScroll
    }
  }),
  mounted() {
    this._initOptions()
    this._initScroll();
  },
  updated() { // 
    this.scroll.refresh();
  },
  methods: {
    _initScroll() { // 
      const { options } = this;
      const { wrapper } = this.$refs;
      if (!this.scroll) {
        // ()
        this.$nextTick(() => {
          this.scroll = new BScroll(wrapper, options);
        });
      }
    },
    _initOptions() { // propsdata
      const _propskey = Object.keys(this._props);
      // prop
      const props = _propskey.reduce((total, key) => {
        if (this._props[key]) {
          total[key] = this._props[key];
        };return total;
      }, {});
      this.options = { ...this.options, ...props };
    }
  }
};
</script>

<style>
</style>


