import Swiper from 'swiper';
import 'swiper/css/swiper.css';
export default function createSwiper() {
    setTimeout(() => {
        new Swiper('.swiper-container-msite-navi', {
            pagination: { // 
                el: '.swiper-pagination'
            },
            // 
            scrollbar: {
                el: '.swiper-scrollbar'
            },
            // swiper
            observer: true,
            grabCursor: true,
        })
    }, 0);
}


