import Vue from 'vue'
import App from './App'
// 
import router from './router'
// 
import store from './store';
// 
import './mock'
// 
import VueLazyload from 'vue-lazyload'
import loading from './assets/img/timg.gif'
Vue.use(VueLazyload, {loading})

new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>',
  store
})


